/* addItem:    action creator ( )      .    item, 
description, image, count, weigth  comments   '   
'ADD_ITEM'   payload,     . */
export const addItem = (item, description, image, count, weigth, comments) => ({
    type: 'ADD_ITEM',
    payload: { item, description, image, count, weigth,comments },
  });
  /* deleteItem:    action creator        . 
     id  
   '   'DELETE_ITEM'   payload,     . */
  export const deleteItem = (id) => ({
    type: 'DELETE_ITEM',
    payload: id,
  });
/*   editItem:    action creator    .   
   id, item, description, image, count  weigth   '   
   'EDIT_ITEM'   payload,   '   id 
      item, description, image, count  weigth */
  export const editItem = (id, item, description, image, count, weigth) => {
    return {
      type: 'EDIT_ITEM',
      payload: { id, newItem: { item, description, image, count, weigth } },
    };
  };
 /*  addItemComments:    action creator       
    .    itemId  comment   
  '   'ADD_COMMENT'   payload,     . */
  export const addItemComments = (itemId, comment) => ({
    type: 'ADD_COMMENT',
    payload: { itemId, comment },
  });
 /*  deleteItemComments:    action creator      
        .    deleteItemId
     commentIndex   '   'DELETE_COMMENT'   payload,     . */
  export const deleteItemComments = (deleteItemId, commentIndex) => {
    return {
      type: 'DELETE_COMMENT',
      payload: {
        deleteItemId,
        commentIndex,
      },
    };
  };
  
